.Preview {
  position: relative;
  width: 100%;
  height: 80%;
  color: white;
  overflow: hidden;
  filter: gray(0.5);

  &.isLoading {
    .images {
      opacity: 0.2;
    }
    .spinner {
      display: block;
    }
  }


  .spinner {
    display: none;
  }

  .images {
    position: relative;
    width: 100%;
    height: 100%;
  }

  img {
    position: absolute;
  }
}
