.ColorPicker {
  position: fixed;
  bottom: 0;

  .color {
    display: inline-block;
    width: 50px;
    height: 50px;
    margin-right: 5px;
    margin-bottom: 5px;
    cursor: pointer;
    border: 1px solid var(--wet-asphalt);
  }

  .color-pipette {
    padding: 5px;
    position: relative;
    top: -11px;
  }

  .chromePicker {
    position: absolute;
    bottom: 75px;
  }
}
