.FacePartContainer {
  display: inline-block;
  width: 150px;
  height: 150px;
  border: 1px solid var(--silver);
  margin-right: 20px;
  margin-bottom: 20px;
  position: relative;
  cursor: pointer;

  &:hover {
    border-color: var(--wet-asphalt);
  }

  &.active {
    border-color: var(--wet-asphalt);
    outline: 1px solid var(--wet-asphalt) !important;
  }

}
